/**
 * Send items upwards.
 */
@-webkit-keyframes upwards-journey {
  from {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  to {
    -webkit-transform: translateY(-2000px);
            transform: translateY(-2000px); } }
@keyframes upwards-journey {
  from {
    -webkit-transform: translateY(0px);
            transform: translateY(0px); }
  to {
    -webkit-transform: translateY(-2000px);
            transform: translateY(-2000px); } }
/* Style the buttons that are used to open the tab content */
.tabs {
  text-align: center; }
  .tabs .tabs-navigation {
    text-align: center; }
    .tabs .tabs-navigation button {
      background: inherit;
      border: none;
      color: #d8d8d8;
      cursor: pointer;
      margin: 0 15px 0 15px;
      outline: none;
      padding: 15px;
      -webkit-transition: 0.3s ease;
      -o-transition: 0.3s ease;
      transition: 0.3s ease; }
    .tabs .tabs-navigation button::after {
      background: #a8a8a8;
      content: '';
      display: block;
      height: 2px;
      margin-top: 5px;
      -webkit-transition: .2s ease;
      -o-transition: .2s ease;
      transition: .2s ease;
      width: 0; }
    .tabs .tabs-navigation button:hover {
      color: #a8a8a8; }
    .tabs .tabs-navigation button.active {
      color: #686868; }
    .tabs .tabs-navigation button.active::after {
      background: #686868;
      width: 100%; }

